
 import { defineComponent, ref } from 'vue'
 export default defineComponent({
   emits: ['taskadd'],

   setup(props, {emit}) {
     let toggled = ref(false)
     let text = ref('')
     let click = () => {
       toggled.value = !toggled.value
     }

     let submit = () => {
       emit('taskadd', text.value)
       text.value = ''
     }
     
     return {
       toggled,
       text,
       click,
       submit
     }
   }
 })
 

